.Main {
    width: 100%;
    height: 100vh;
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .Sidebar {
    height: 100%;
    width: 250px;
    background: #2d445d;
  }
  
  .SidebarList {
    height: auto;
    padding: 0;
    width: 100%;
  }
  
  .SidebarList .row {
    width: 100%;
    height: 60px;
    /* background-color: cadetblue; */
    /* border: 1px solid black; */
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
  }

  /*カーソル当たった時の色変更*/
  .SidebarList .row:hover {
    cursor: pointer;
    background-color: #3b627a;
  }
  
  /*今のアクティブパスの色を変更*/
  .SidebarList #active {
    background-color: #3b627a;
  }
  

  .row #icon {
    flex: 30%;
    display: grid;
    place-items: center;
  }
  
  .row #title {
    flex: 70%;
  }
  
  .SidebarIcon {
    padding-top: 20px;
    padding-bottom: 15px;
    text-align: center;
  }
  
  .SidebarIcon img {
    width: 100px;
    border-radius: 50%; /* 画像を丸く切り抜く */
  }
  
  .SidebarIcon p {
    color: white;
    font-weight: 600;
    font-size: 13px;
  }